@import "src/styles/screen";

.footer {
    padding: 20px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    border-top: 1px solid rgba(89, 103, 154, 0.3);

    @media (max-width: $sm) {
        padding: 15px 0;
    }

    p {
        font-weight: 400;
        font-size: var(--text-m-size);
        color: var(--white-clr);

        @media (max-width: $sm) {
            font-size: var(--text-s-size);
        }
    }
}