@import "src/styles/screen";

.catalog {
    width: 100%;
    height: 100%;

    &__wrapper {
        margin: 40px 0;
        height: inherit;
        display: flex;
        justify-content: space-between;
    }

    &__content {
        position: relative;
        display: flex;
        align-items: center;
        flex-direction: column;
        width: 70%;

        @media (max-width: $md) {
            width: 100%;
        }
    }
}