@import "src/styles/screen";

.inputBlock {
    width: 100%;
}

.inputBlock__item {
    width: 100%;
    padding: 15px 20px;
    border: 2px solid var(--white-clr);
    background: transparent;
    outline: none;
    color: var(--white-clr);
    font-weight: 400;
    font-size: var(--text-m-size);
    line-height: 2rem;
    transition: all .2s ease-in-out;

    @media (max-width: $md) {
        padding: 10px 15px;
    }
    @media (max-width: $sm) {
        padding: 8px 12px;
        font-size: var(--text-s-size);
    }

    &_error {
        border-color: red !important;
    }

    &::placeholder {
        color: inherit;
        font-weight: inherit;
        font-size: inherit;
        line-height: inherit;
    }

    &:focus, &:active {
        border: 2px solid var(--accent-clr);
    }
}

.inputBlock__error {
    display: inline-block;
    margin-top: 5px;
    color: red;
    font-size: var(--text-s-size);
}

.label {
    transition: all .2s ease-in-out;
    border: 1px solid transparent;

    &_isActive {
        border-color: var(--accent-clr);
    }

    &:hover {
        border-color: var(--accent-clr);
    }
}


