@import "src/styles/screen";

.btn {
    display: none;
    position: absolute;
    right: 0;
    width: 60px;
    height: 100%;
    padding: 0;
    background: var(--accent-clr);
    padding: 10px;
    z-index: 1001;

    @media (max-width: $md) {
        display: block;
    }
    @media (max-width: $sm) {
        width: 50px;
    }
    @media (max-width: $xs) {
        width: 40px;
    }

    &__burger {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 8px;
        width: 100%;
        height: 100%;
        transition: all .2s ease-in-out;
        will-change: tranform;

        @media (max-width: $xs) {
            gap: 5px;
        }

        span {
            display: block;
            background: var(--white-clr);
            height: 3px;
            width: 100%;
            will-change: tranform;
            transition: all .2s ease-in-out;

            @media (max-width: $xs) {
                height: 2px;
            }
        }
    }
}

._isOpen {
    gap: 0;
    span {
        &:nth-child(1) {
            transform: translate(0, 100%) rotate(-135deg);
        }
        &:nth-child(2) {
            opacity: 0;
        }
        &:nth-child(3) {
            transform: translate(0, -100%) rotate(135deg);
        }
    }
}