@import "src/styles/screen";

.button {
    display: inline-block;
    position: relative;
    padding: 15px 20px;
    border: 2px solid var(--white-clr);

    font-weight: 400;
    font-size: var(--text-m-size);
    line-height: 2rem;
    color: var(--white-clr);
    transition: all .15s;
    z-index: 0;

    @media (max-width: $md) {
        padding: 5px 10px;
    }
    @media (max-width: $sm) {
        padding: 5px 10px;
        font-size: var(--text-s-size);
    }

    &[name="addInfo"] {
        padding: 10px 15px;

        @media (max-width: $md) {
            padding: 5px 10px;
        }
    }

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 0;
        background: var(--accent-clr);
        transition: all .2s ease-in-out;
        z-index: -1;
    }

    &:not(:disabled):hover {
        border: 2px solid var(--accent-clr);
        &::before {
            width: 100%;
        }
    }

    &:disabled {
        cursor: default;
        opacity: 0.5;
    }

}
