@import "src/styles/screen";

.select {
    cursor: pointer;
    position: relative;
    align-self: flex-end;
    width: 100%;

    button {
        width: 100%;
        padding: 10px;
        background: var(--bg-element-clr);
        color: var(--white-clr);
        font-size: var(--text-m-size);
        border: 1px solid transparent;
        transition: all .2s ease-in-out;

        @media (max-width: $xs) {
            padding: 5px;
            font-size: var(--text-s-size);
        }

        &:hover {
            border-color: var(--accent-clr);
        }
    }

    .options {
        display: none;
        position: absolute;
        top: 100%;
        left: 0;
        width: 100%;
        z-index: 100;

        li {
            padding: 10px 15px;
            background: var(--black-clr);
            color: var(--white-clr);
            font-size: var(--text-m-size);
            transition: all .2s ease-in-out;
            border: 1px solid transparent;

            @media (max-width: $sm) {
                font-size: var(--text-s-size);
            }

            &:first-child {
                text-align: left;
                color: #676767;
                font-style: italic;
                cursor: default;
            }

            &:not(:first-child):hover {
                border-color: var(--accent-clr);
                color: var(--accent-clr);
            }
        }
    }

    .show {
        display: block;
    }
}
