@import "src/styles/screen";

.notFound {
    height: 100%;

    &__wrapper {
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        text-align: center;

        h1 {
            font-weight: 800;
            font-size: 9.6rem;
            line-height: 11.8rem;
            color: var(--white-clr);

            @media (max-width: $md) {
                font-size: 6.4rem;
                line-height: 7.2rem;
            }

            span {
                color: var(--accent-clr);
            }
        }
        p {
            font-weight: 800;
            font-size: var(--text-xl-size);
            color: var(--white-clr);

            @media (max-width: $md) {
                font-size: var(--text-m-size);
            }
        }
        a {
            margin-top: 20px;
            font-weight: 400;
            font-size: var(--text-l-size);
            text-decoration-line: underline;
            color: var(--accent-clr);

            @media (max-width: $md) {
                font-size: var(--text-m-size);
                margin-top: 5px;
            }
        }
    }
}
