@import "src/styles/screen";

.filters__item {
    background: var(--bg-element-clr);
    padding: 15px 20px;
    font-weight: 400;
    font-size: var(--text-m-size);
    color: var(--white-clr);
    text-align: left;
    border: 1px solid transparent;
    transition: all .2s ease-in-out;

    @media (max-width: $md) {
        background: none;
    }
    @media (max-width: $xs) {
        padding: 10px 15px;
    }

    &._isActive {
        border: 1px solid var(--accent-clr);
        color: var(--accent-clr);
    }

    &:hover {
        border: 1px solid var(--accent-clr);
        color: var(--accent-clr);
    }
}