@import "src/styles/screen";

.filters {
    position: relative;
    width: 30%;
    display: flex;
    flex-direction: column;
    gap: 15px;
    margin-right: 20px;

    @media (max-width: $md) {
        justify-content: center;
        position: absolute;
        top: 0;
        left: 0;
        gap: 0;
        background: var(--overlay-clr);
        transform: translate(-100%, 0);
        transition: transform .2s ease-in-out;
        margin-right: 0;
        width: 70%;
        height: 100%;
        z-index: 999;
    }

    &_isOpen {
        transform: translate(0, 0);
    }

    &__close {
        display: none;
        position: absolute;
        width: 40px;
        height: 40px;
        bottom: 200px;
        right: -40px;
        background: var(--accent-clr);
        padding: 0;
        transition: bottom .2s ease-in-out;

        @media (max-width: $md) {
            display: flex;
            justify-content: center;
            align-items: center;
        }

        div {
            display: inline-flex;
            flex-direction: column;
            gap: 3px;
            transition: transform .2s ease-in-out;
        }
        span {
            display: block;
            width: 5px;
            height: 5px;
            background: var(--white-clr);
            border-radius: 50%;
            margin: 0 auto;
        }

        &_isOpen {
            bottom: 50%;
            div {
                transform: rotate(90deg);
            }
        }
    }
}
