@import "src/styles/screen";

.textarea {
    width: 100%;
    min-height: 80px;
    max-height: 100px;
    resize: none;
    padding: 15px 20px;
    border: 2px solid var(--white-clr);
    background: transparent;
    outline: none;
    color: var(--white-clr);
    font-weight: 400;
    font-size: var(--text-m-size);
    line-height: 2rem;
    transition: all .2s ease-in-out;

    @media (max-width: $md) {
        padding: 10px 15px;
    }
    @media (max-width: $sm) {
        padding: 8px 12px;
        font-size: var(--text-s-size);
        min-height: 50px;
        max-height: 80px;
    }

    &::placeholder {
        color: inherit;
        font-weight: inherit;
        font-size: inherit;
        line-height: inherit;
    }

    &:focus, &:active {
        border: 2px solid var(--accent-clr);
    }
}
