@import "src/styles/screen";

.products {
    max-width: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px 0 0 0;

    &__list {
        width: inherit;
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        justify-content: space-between;
        gap: 20px;

        @media (max-width: $lg) {
            grid-template-columns: repeat(3, 1fr);
        }
        @media (max-width: $sm) {
            grid-template-columns: repeat(2, minmax(120px, 1fr));
        }
        @media (max-width: $xs) {
            grid-template-columns: repeat(1, minmax(120px, 1fr));
            justify-content: center;
        }

        &_empty {
            padding: 80px 0;
            color: var(--white-clr);
            font-size: var(--text-l-size);
            text-align: center;
        }
    }

    &__sort {
        background: var(--bg-element-clr);
        outline: none;
        border: none;
        color: var(--white-clr);
        padding: 10px 15px;

        option {
            background: var(--bg-element-clr);
        }
    }
}