@import "src/styles/screen";

.basket__item {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: var(--bg-element-clr);
    padding: 10px 15px;
    color: var(--white-clr);

    @media (max-width: $sm) {
        flex-direction: column;
        padding: 10px;
        gap: 10px;
    }

    &_num {
        font-weight: 400;
        font-size: var(--text-m-size);

        @media (max-width: $sm) {
            font-size: var(--text-s-size);
            align-self: flex-start;
        }
    }

    &_main {
        @media (max-width: $sm) {
            text-align: center;
        }
        a {
            font-weight: 700;
            font-size: var(--text-m-size);

            @media (max-width: $sm) {
                font-size: var(--text-s-size);
            }
        }
        p {
            font-weight: 400;
            font-size: var(--text-s-size);

            @media (min-width: $sm) and (max-width: $md) {
                text-overflow: ellipsis;
                white-space: nowrap;
                overflow-x: hidden;
                max-width: 250px;
            }
            @media (max-width: $sm) {
                font-size: var(--text-xs-size);
                margin-top: 5px;
            }
        }
    }

    &_price {
        font-weight: 400;
        font-size: var(--text-m-size);
    }
}