@import "src/styles/screen";

.rating__stars {
    display: flex;
    gap: 20px;
    align-items: center;
    justify-content: center;
    margin-top: 30px;

    @media (max-width: $md) {
        gap: 10px;
        margin-top: 10px;
    }
}

.rating__star {
    cursor: pointer;

    @media (max-width: $sm) {
        svg {
            width: 30px;
            height: 30px;
        }
    }
}