@import "src/styles/screen";

.form {
    max-width: 750px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    padding: 40px 80px;
    background: var(--bg-element-clr);

    @media (max-width: $md) {
        padding: 20px 40px;
    }
    @media (max-width: $sm) {
        gap: 15px;
    }

    .error {
        color: red;
        font-size: var(--text-m-size);
        font-weight: 400;
    }

    h2 {
        font-weight: 700;
        font-size: var(--text-l-size);
        line-height: 3.9rem;
        color: var(--white-clr);
        margin-bottom: 40px;

        @media (max-width: $md) {
            margin-bottom: 20px;
        }
        @media (max-width: $sm) {
            margin-bottom: 10px;
        }
    }

    &__footer {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;

        p {
            font-weight: 400;
            font-size: var(--text-m-size);
            line-height: 2rem;
            color: #FFFFFF;
            @media (max-width: $sm) {
                font-size: var(--text-s-size);
                line-height: 1.5rem;
                display: flex;
                flex-wrap: wrap;
            }
            a {
                color: var(--accent-clr);
                font-weight: 700;
                text-decoration: underline;
            }
        }
    }
}