@import "src/styles/screen";

.header {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 60px 0 0 0;

    @media (max-width: $md) {
        margin: 40px 0 0 0;
    }

    &__logo {
        font-size: var(--text-2xl-size);
        color: var(--white-clr);

        @media (max-width: $sm) {
            font-size: var(--text-xl-size);
        }

        span {
            color: var(--accent-clr);
        }
    }
}