@import "src/styles/screen";

.counter {
    button {
        background: var(--black-clr);
        width: 30px;
        height: 30px;
        padding: 0;
        font-size: var(--text-m-size);
        border: 1px solid transparent;
        transition: all .2s ease-in-out;

        @media (max-width: $md) {
            width: 25px;
            height: 25px;
        }

        &:hover {
            border-color: var(--accent-clr);
        }
        &:disabled {
            opacity: .5;
        }
    }
    span {
        font-size: var(--text-m-size);
        font-weight: 400;
        padding: 0 15px;
        color: var(--white-clr);

        @media (max-width: $md) {
            padding: 0 10px;
        }
    }
}