@import "src/styles/screen";

.header {
    position: relative;
    padding: 15px 0;
    border-bottom: 1px solid rgba(89, 103, 154, 0.3);

    @media (max-width: $sm) {
        padding: 10px 0;
    }
    @media (max-width: $xs) {
        padding: 5px 0;
    }

    &__wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

.logo {
    font-size: var(--text-l-size);
    color: var(--white-clr);
    font-weight: 750;
    @media (max-width: $md) {
        font-size: 2.4rem;
    }
    span {
        color: var(--accent-clr);
    }
}