* {
    box-sizing: border-box;
    font-family: "Avenir Next Cyr", sans-serif;
}

*::-webkit-scrollbar {
    width: 3px;

    @media (max-width: $sm) {
        width: 2px
    }
}

*::-webkit-scrollbar-track {
    background: var(--white-clr);
}

*::-webkit-scrollbar-thumb {
    background-color: var(--accent-clr);
}

html, body {
    height: 100%;
    width: 100%;
    font-size: 10px;
    overflow-x: hidden;
}

body {
    padding: 0;
    margin: 0;
    background: var(--bg-clr);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

h1, h2, h3, h4, h5, h6, p {
    margin: 0;
    padding: 0;
}


ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

a {
    text-decoration: none;
    color: inherit;
}

svg > path {
    transition: all .2s
}

button {
    outline: none;
    border: none;
    background: none;
    color: inherit;
    cursor: pointer;
    font-size: inherit;
    font-weight: inherit;
}

#root {
    height: 100%;
    display: flex;
    flex-direction: column;
}

.main {
    flex: 1 1 auto;
}

.container {
    max-width: 1570px;
    width: 100%;
    padding: 0 20px;
    margin: 0 auto;
    height: 100%;

    @media (max-width: $sm) {
        padding: 0 15px;
    }
}