:root {
    --text-s-size: 1.4rem;
    --text-m-size: 1.6rem;
    --text-l-size: 3.2rem;
    --text-xl-size: 4rem;
    --text-2xl-size: 6.4rem;

    --white-clr: #FFFFFF;
    --accent-clr: #59679A;
    --yellow-clr: #DDE775;
    --black-clr: rgba(18, 19, 21, 1);
    --overlay-clr: rgba(18, 19, 21, 0.9);
    --bg-element-clr: rgba(176, 176, 176, 0.08);
    --bg-clr: linear-gradient(106.47deg, #191A24 20.07%, #202134 93.84%);

    @media (max-width: $md) {
        --text-s-size: 1.2rem;
        --text-m-size: 1.4rem;
        --text-l-size: 2.2rem;
        --text-xl-size: 2.8rem;
        --text-2xl-size: 4.2rem;
    }
}