@import "src/styles/screen";

.products__card {
    position: relative;
    width: 100%;
    background: var(--bg-element-clr);
    cursor: pointer;
    overflow: hidden;
    transition: all .2s ease-in-out;

    &:hover {
        .products__card_banner {
            border-color: var(--accent-clr);
        }
    }

    img {
        width: 100%;
        height: 240px;
        object-fit: cover;
        object-position: center;

        @media (max-width: $lg) {
            height: 220px;
        }
        @media (max-width: $md) {
            height: 180px;
        }
    }

    &_banner {
        position: absolute;
        left: 0;
        bottom: 0;
        opacity: 1;
        width: 100%;
        display: flex;
        justify-content: center;
        flex-direction: column;
        gap: 10px;
        transition: all .2s ease-in-out;
        padding: 5px;
        min-height: 60px;
        border: 1px solid transparent;
        background: var(--black-clr);
        overflow: hidden;

        @media (max-width: $md) {
            min-height: auto;
        }
        @media (max-width: $sm) {
            gap: 5px;
        }

        div {
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 5px;
        }
        h3, p {
            font-weight: 400;
            font-size:var(--text-s-size);
            color: var(--white-clr);

            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
        }
        .title {
            width: 168px;
        }
        .rating {
            display: flex;
            align-items: center;
            gap: 3px;
            font-weight: 800;
            font-size: 1rem;
            line-height: 15px;
            color: var(--white-clr);
        }
        .price {
            font-weight: 800;
            font-size: 1.2rem;
            line-height: 15px;
            white-space: nowrap;
            color: var(--white-clr);
        }
    }
}