@import "src/styles/screen";

.admin {
    min-height: 100%;
    display: flex;
    align-items: center;

    .form__info {
        padding: 10px 0 10px 0;
        display: flex;
        width: 100%;
        flex-direction: column;
        gap: 20px;
        max-height: 225px;
        overflow-y: auto;

        @media (max-width: $sm) {
            padding: 8px 8px 8px 0;
            gap: 15px;
        }

        &_item {
            display: flex;
            gap: 10px;

            @media (max-width: $sm) {
                flex-direction: column;
            }

            button {
                @media (max-width: $sm) {
                    width: 50%;
                }
            }

            button:hover {
                border-color: #b14d4d;
            }
            button::before {
                background: #b14d4d;
            }
        }
    }

    &__wrapper {
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;

        h1 {
            font-weight: 700;
            font-size: var(--text-xl-size);
            color: var(--white-clr);
        }
    }

    &__panel {
        margin: 50px 0 0 0;
        display: flex;
        flex-direction: column;
        gap: 20px;
        max-width: 1010px;
        width: 100%;

        @media (max-width: $md) {
            gap: 15px;
            margin: 30px 0 0 0;
        }
        @media (max-width: $sm) {
            margin: 20px 0 0 0;
        }

        li button {
            width: 100%;
            background-color: var(--bg-element-clr);
            padding: 35px 20px;

            font-weight: 400;
            font-size: var(--text-m-size);
            color: var(--white-clr);
            border: 1px solid transparent;
            transition: all .2s ease-in-out;

            @media (max-width: $md) {
                padding: 20px 10px;
            }
            @media (max-width: $sm) {
                padding: 15px 5px;
            }

            &:hover {
                border: 1px solid var(--accent-clr);
                color: var(--accent-clr);
            }
        }
    }
}
