@import "src/styles/screen";

.product {
    margin: 40px 0;

    @media (max-width: $sm) {
        margin: 20px 0;
    }

    &__wrapper {
        display: flex;
        flex-direction: column;
        gap: 30px;
    }

    &__top {
        display: flex;
        gap: 250px;

        @media (max-width: $lg) {
            gap: 100px;
        }
        @media (max-width: $md) {
            gap: 50px;
        }
        @media (max-width: $sm) {
            gap: 25px;
        }
        @media (max-width: $xs) {
            flex-direction: column;
        }
    }

    &__bottom {
        display: flex;
        flex-direction: column;
        gap: 30px;
        margin-top: 50px;

        @media (max-width: $md) {
            margin-top: 20px;
            gap: 20px;
        }
        @media (max-width: $sm) {
            margin-top: 0;
        }
    }

    &__desription {
        h2 {
            font-weight: 700;
            font-size: var(--text-l-size);
            color: var(--white-clr);
        }
        p {
            font-weight: 400;
            font-size: var(--text-m-size);
            color: var(--white-clr);
            margin-top: 20px;

            @media (max-width: $sm) {
                font-size: var(--text-s-size);
                margin-top: 10px;
            }
        }
    }

    &__characts {
        h2 {
            font-weight: 700;
            font-size: var(--text-l-size);
            color: var(--white-clr);
        }
        &_list {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            margin-top: 20px;

            li {
                display: flex;
                width: 100%;
                padding: 10px 15px;

                &:nth-child(odd) {
                    background-color: var(--bg-element-clr);
                }

                @media (max-width: $sm) {
                    justify-content: space-between;
                }
            }
            h3 {
                font-weight: 500;
                font-size: var(--text-m-size);
                color: var(--white-clr);

                @media (max-width: $sm) {
                    font-size: var(--text-s-size);
                }
            }
            p {
                font-weight: 400;
                font-size: var(--text-m-size);
                color: var(--white-clr);

                @media (max-width: $sm) {
                    font-size: var(--text-s-size);
                }
            }
        }
    }

    &__image {
        max-width: 400px;
        max-height: 480px;
        width: 100%;
        height: 480px;
        background-color: var(--bg-element-clr);
        transition: all .2s ease-in-out;

        @media (max-width: $lg) {
            max-width: 300px;
            height: 380px;
        }
        @media (max-width: $md) {
            max-width: 200px;
            height: 280px;
        }
        @media (max-width: $sm) {
            max-width: 150px;
            height: 230px;
        }
        @media (max-width: $xs) {
            margin: 0 auto;
            max-width: 100%;
            height: auto;
        }

        &:hover {
            background-color: transparent;
            transform: scale(1.05);
        }
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center;
        }
    }

    &__info {
        display: flex;
        flex: 1;
        flex-direction: column;
        align-items: flex-start;
        padding-top: 60px;

        @media (max-width: $lg) {
            padding-top: 40px;
        }
        @media (max-width: $md) {
            padding-top: 20px;
        }
        @media (max-width: $xs) {
            padding-top: 0;
        }

        &_row {
            display: flex;
            width: 100%;
            justify-content: space-between;
            align-items: center;
            margin: 20px 0 0 0;

            @media (max-width: $md) {
                margin: 15px 0 0 0;
            }

            p {
                font-weight: 750;
                font-size: var(--text-xl-size);
                color: var(--white-clr);

                @media (max-width: $lg) {
                    font-size: var(--text-l-size);
                }
                @media (max-width: $sm) {
                    font-size: var(--text-m-size);
                }
            }

            span {
                font-weight: 750;
                font-size: var(--text-l-size);
                color: var(--white-clr);

                display: flex;
                align-items: center;
                gap: 15px;
                cursor: pointer;

                @media (max-width: $md) {
                    gap: 5px;
                    svg {
                        width: 25px;
                        height: 25px;
                    }
                }
                @media (max-width: $sm) {
                    font-size: var(--text-m-size);
                    svg {
                        width: 20px;
                        height: 20px;
                    }
                }
            }
        }

        h1 {
            font-weight: 800;
            font-size: var(--text-l-size);
            color: var(--white-clr);

            @media (max-width: $sm) {
                font-size: var(--text-m-size);
            }
        }

        button {
            margin: 30px 0 0 0;
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 13px;

            @media (max-width: $md) {
                margin: 15px 0 0 0;
            }
        }
    }
}
