@import "src/styles/screen";

.nav {
    @media (max-width: $md) {
        position: fixed;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        min-height: 100%;
        transform: translate3d(0, -100%, 0);
        top: 0;
        left: 0;
        background: var(--overlay-clr);
        transition: all .2s ease-in-out;
        z-index: 1000;
    }
    &__list {
        display: flex;
        align-items: center;
        gap: 2rem;

        @media (max-width: $md) {
            flex-direction: column;
            gap: 3rem;
        }
        @media (max-width: $sm) {
            flex-direction: column;
            gap: 2rem;
        }

        &_item {
            position: relative;
            transition: all .2s;
            font-size: var(--text-m-size);
            font-weight: 500;
            color: var(--white-clr);

            &::before {
                position: absolute;
                content: '';
                width: 0;
                height: 1px;
                left: 0;
                right: 0;
                margin: 0 auto;
                bottom: -5px;
                opacity: 0;
                background-color: var(--accent-clr);
                transition: all .25s ease-in-out;
            }
            &:hover {
                color: var(--accent-clr);
            }
            &:hover > a > svg > path {
                fill: var(--accent-clr);
            }
            &:hover::before {
                opacity: 1;
                width: 50%;
            }

            @media (max-width: $md) {
                font-size: var(--text-l-size);
                transition: opacity .3s ease-in-out;
                opacity: 0;
            }
            @media (max-width: $sm) {
                font-size: var(--text-m-size);
            }
        }
        .isActive {
            color: var(--accent-clr);
            &::before {
                width: 50%;
                opacity: 1;
            }
        }
    }
}

._isOpen {
    transform: translate3d(0, 0, 0);
    ul li {
        opacity: 1;
    }
}
