.pagination {
    display: flex;
    gap: 10px;
    margin-top: 40px;

    button {
        width: 30px;
        height: 30px;
        line-height: 15px;
        font-weight: 700;
        font-size: var(--text-m-size);
        color: var(--white-clr);
        background: transparent;
        transition: all .2s ease-in-out;

        &._isActive {
            color: var(--accent-clr);
            background: var(--bg-element-clr);
        }

        &:hover {
            color: var(--accent-clr);
            background: var(--bg-element-clr);
        }
    }

    span {
        font-weight: 700;
        font-size: var(--text-m-size);
        color: var(--white-clr);
    }

}
