@import "src/styles/screen";

.basket {
    height: 100%;

    &__wrapper {
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &__content {
        width: 100%;
        display: flex;
        flex-direction: column;
    }

    &__total {
        text-align: right;
        font-size: var(--text-l-size);
        font-weight: 400;
        color: var(--white-clr);

        span {
            font-weight: 700;
        }
    }

    &__empty {
        font-weight: 400;
        font-size: var(--text-xl-size);
        text-align: center;
        color: var(--white-clr);

        @media (max-width: $md) {
            font-size: var(--text-l-size);
        }
        @media (max-width: $sm) {
            font-size: var(--text-m-size);
        }

        a {
            font-size: var(--text-l-size);
            color: var(--accent-clr);
            text-decoration: underline;

            @media (max-width: $sm) {
                font-size: var(--text-m-size);
            }
        }
    }
}
