.alert {
    position: fixed;
    top: 10%;
    right: 20px;
    transform: translate(0, -10%);
    width: auto;
    padding: 10px;
    background-color: var(--bg-element-clr);
    z-index: 1000;

    p {
        font-size: var(--text-s-size);
        color: var(--white-clr);
        font-weight: 400;
        text-align: left;
    }
}