@import "src/styles/screen";

.home {
    min-height: 100%;
    display: flex;
    align-items: center;
    &__wrapper {
        display: flex;
        justify-content: center;
        align-items: center;
    }
}

.homeBanner {
    position: relative;
    max-width: 1010px;
    width: 100%;
    padding: 150px 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: var(--bg-element-clr);

    @media (max-width: $md) {
        padding: 100px 20px;
    }

    &__start {
        position: absolute;
        bottom: 0;
        right: 0;
        display: flex;
        align-items: center;
        font-weight: 750;
        font-size: var(--text-l-size);
        line-height: 39px;
        color: var(--white-clr);
        cursor: pointer;
        transition: all .3s;
        z-index: 1;

        @media (max-width: $md) {
            font-size: var(--text-m-size);
        }

        &::after {
            display: block;
            position: absolute;
            content: '';
            width: 0;
            height: 100%;
            right: 0;
            top: 0;
            background: var(--accent-clr);
            transition: all .2s;
            z-index: -1;
        }

        &:hover::after {
            width: 100%;
        }

        span {
            display: block;
            height: 100%;
            margin: 0 12px;
        }

        button {
            width: 80px;
            height: 80px;
            background: var(--accent-clr);
            display: flex;
            justify-content: center;
            align-items: center;
            transition: all .2s ease-out;

            @media (max-width: $md) {
                width: 50px;
                height: 50px;
            }

            svg {
                width: 60%;
                height: 60%;
            }
        }
    }

    h1 {
        font-size: var(--text-xl-size);
        text-align: center;
        color: var(--white-clr);

        @media (max-width: $md) {
            font-size: var(--text-l-size);
        }

        span {
            color: var(--accent-clr);
        }
    }
}