@import "src/styles/screen";

.modal {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    background-color: rgba(18, 19, 21, 0.6);
    z-index: 999;
    overflow-y: auto;

    &__wrapper {
        overflow-y: auto;

        @media (max-width: $xl) {
            max-height: 80vh
        }
        @media (max-width: $md) {
            max-height: 70vh
        }
    }

    h2 {
        text-align: center;
        font-size: var(--text-l-size);
        font-weight: 500;
        color: var(--white-clr);
    }

    form {
        margin-top: 20px;
        padding-right: 40px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 15px;

        @media (max-width: $md) {
            padding-right: 25px;
        }
        @media (max-width: $sm) {
            gap: 12px;
            padding-right: 15px;
        }

        label {
            cursor:pointer;
            width: 100%;
            text-align: center;
            padding: 10px;
            background: var(--bg-element-clr);
            color: var(--white-clr);
            font-size: var(--text-m-size);

            @media (max-width: $sm) {
                padding: 5px;
                font-size: var(--text-s-size);
            }

            input {
                display: none;
            }
        }
    }

    &__container {
        max-width: 600px;
        width: 100%;
        padding: 40px 0 40px 40px;
        background-color: var(--black-clr);

        @media (max-width: $md) {
            padding: 25px 0 25px 25px;
            margin: 0 20px;
        }
        @media (max-width: $sm) {
            padding: 15px 0 15px 15px;
            margin: 0 15px;
        }
    }
}

.show {
    display: flex;
}